import React, { useState, useEffect } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { NavLink } from "react-router-dom";
import {
  faUserCircle,
  faChevronRight,
  faHome,
  faHamburger,
  faToiletPaper,
  faSignOutAlt,
} from "@fortawesome/free-solid-svg-icons";
import firebase, { logOut } from "../../assets/firebase/firebase";

const Navigation = () => {
  const [isMenuExpand, setIsMenuExpand] = useState(true);
  const [isUser, setIsUser] = useState("");

  useEffect(() => {
    const authListener = () => {
      firebase.auth().onAuthStateChanged((user) => {
        if (user) {
          setIsUser(user);
        } else {
          setIsUser("");
        }
      });
    };
    authListener();
  }, []);

  const navExpand = isMenuExpand
    ? "Nav__expand Nav__expand--rotate"
    : "Nav__expand";

  const navIcons = isMenuExpand
    ? "Nav__menu-icons Nav__menu-icons--expand"
    : "Nav__menu-icons";

  const navList = isMenuExpand ? "Nav__list Nav__list--expand" : "Nav__list";

  const navigation = isMenuExpand ? "Nav Nav__box-expand" : "Nav";

  return (
    <nav className={navigation}>
      <ul className={navList}>
        <li
          onClick={() => setIsMenuExpand(!isMenuExpand)}
          className={navExpand}
        >
          <FontAwesomeIcon icon={faChevronRight} />
        </li>

        <li>
          <NavLink className={navIcons} exact to="/">
            <FontAwesomeIcon icon={faHome} />
          </NavLink>
        </li>

        <li>
          <NavLink className={navIcons} to="/foodBook">
            <FontAwesomeIcon icon={faHamburger} />
          </NavLink>
        </li>

        <li>
          <NavLink className={navIcons} to="/training">
            <FontAwesomeIcon icon={faToiletPaper} />
          </NavLink>
        </li>

        <li>
          {isUser ? (
            <NavLink to='/login' className={navIcons} onClick={logOut}>
              <FontAwesomeIcon icon={faSignOutAlt} />
            </NavLink>
          ) : (
            <NavLink className={navIcons} to="/login">
              <FontAwesomeIcon icon={faUserCircle} />
            </NavLink>
          )}
        </li>
      </ul>
    </nav>
  );
};

export default Navigation;
