import React from "react";
import foodBag from "../../assets/foodBag.svg";

const Home = () => {
  return (
    <div className="Home">
      <img className="Home__foodBag" src={foodBag} alt="Food Bag" />
      <h1 className="Home__header">Witaj na Food Site!</h1>
    </div>
  );
};

export default Home;
